main {
  position: position;
  overflow: hidden;
  width: 100%;
  height: 100vh;
}

.list li {
  color: #AAA;
}
.list li a {
  padding: .5em 1.2em;
  display: block;
}
.list li a::before {
  right: 1.2em;
  color: #AAA;
}
.list li:first-child {
  border-top: 0;
}
.content {
  padding: 1em;
}


